<template>
    <universal-layout>
        <template v-slot:header>
            <el-form :inline="true" :model="formData" class="demo-form-inline">
                <el-form-item label="公司名">
                    <el-input v-model="formData.companyName" placeholder="请输入用户名" clearable />
                </el-form-item>
                <el-form-item label="负责人">
                    <el-input v-model="formData.head" placeholder="请输入岗位" clearable />
                </el-form-item>
                <el-form-item label="培训老师">
                    <el-input v-model="formData.trainTeachers" placeholder="请输入组织" clearable />
                </el-form-item>
                <el-form-item label="联系方式">
                    <el-input v-model="formData.tel" placeholder="请输入手机号" clearable />
                </el-form-item>
                <el-form-item label="时间">
                    <el-date-picker
                        v-model="formData.time"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始时间"
                        end-placeholder="结束时间"
                    />
                </el-form-item>
                <el-form-item label="状态">
                    <el-select v-model="formData.status">
                        <el-option vlaue="状态一" label="状态一"></el-option>
                        <el-option vlaue="状态二" label="状态二"></el-option>
                        <el-option vlaue="状态三" label="状态三"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button @click="searchBut">搜索</el-button>
                </el-form-item>
            </el-form>
        </template>
        <template v-slot:main>
            <!-- 表格 -->
            <div style="background-color: white; box-sizing: border-box">
                <el-table
                    :data="tableData"
                    empty-text="没有数据"
                    :header-cell-style="{
                        background: '#eef1f6',
                        color: '#3D3D3D',
                        fontSize: '12px',
                        height: '44px'
                    }"
                    @selection-change="multipleSelectionClick"
                >
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column prop="user" label="公司名称" width="180" align="center" />
                    <el-table-column prop="post" label="负责人" width="180" align="center" />
                    <el-table-column prop="organization" label="手机号" align="center" />
                    <el-table-column prop="content" label="所属培训老师" align="center" />
                    <el-table-column prop="license" label="营业执照" align="center" />
                    <el-table-column prop="companyAddress" label="公司地址" align="center" />
                    <el-table-column prop="currentStatus" label="当前状态" align="center" />
                    <el-table-column prop="applicationTimeline" label="申请时间" align="center" />
                </el-table>
            </div>
        </template>
        <template v-slot:footer>
            <div>
                <!-- 分页 -->
                <div class="pagination-contr">
                    <el-pagination
                        v-model:current-page="paginator.currentPage"
                        v-model:page-size="paginator.pageSize"
                        :page-sizes="[16, 26, 36, 46]"
                        layout="->,total, sizes, prev, pager, next, jumper"
                        :total="paginator.total"
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                    />
                </div>
                <div
                    style="
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        height: 72px;
                        box-shadow: 0px -5px 7px 0px rgba(223, 113, 113, 0.06);
                        background-color: white;
                    "
                >
                    <el-button @click="approval(0)">不通过</el-button>
                    <el-button @click="approval(1)">通过</el-button>
                </div>
            </div>
        </template>
    </universal-layout>
    <!-- <div class="search-aira"></div> -->
</template>

<script lang="ts" setup>
import http from '@/util/http'
import { onMounted, reactive, ref } from 'vue'

// 搜索数据
const formData = reactive<any>({})
// 查询内容
const search = () => {
    http({
        url: 'https://mock.apifox.com/m1/2117465-3078121-default/request',
        method: 'POST',
        data: {
            currentPage: paginator.currentPage,
            pageSize: paginator.pageSize,
            ...formData
        }
    }).then(({ data }) => {
        // 设置表格数据
        tableData = Object.assign(tableData, data.dadaList)
        // 设置分页大小
        paginator.total = Number(data.total)
        paginator.currentPage = data.currentPage
        paginator.pageSize = data.pageSize
    })
}

// 表格数据
let tableData = reactive([])

// 分页
const paginator = reactive({
    currentPage: 1,
    pageSize: 16,
    total: 0
})

// 修改分页大小
const handleSizeChange = (val: number) => {
    console.log(`${val} items per page`)
}

// 切换当前页
const handleCurrentChange = (val: number) => {
    console.log(`current page: ${val}`)
    paginator.currentPage = val
    search()
}

// 查询按钮
const searchBut = () => {
    paginator.currentPage = 1
    search()
}

// 多选
let multipleSelection = ref([])
// 表格多选事件
let multipleSelectionClick = (row: any) => {
    multipleSelection.value = row
    console.log(multipleSelection.value)
}
// 审批
let approval = (val: 0 | 1) => {
    let batchApprovals = []
    switch (val) {
        case 0:
            batchApprovals = multipleSelection.value.map((item: { id: number }) => item.id)
            multipleSelection.value = []
            alert('不通过' + batchApprovals)
            break
        case 1:
            batchApprovals = multipleSelection.value.map((item: { id: number }) => item.id)
            multipleSelection.value = []
            alert('通过' + batchApprovals)
            break
    }
}
search()
</script>

<style lang="scss" scoped>
// .search-aira {
//     box-sizing: border-box;
//     padding: 20px;
//     background-color: rgb(239, 239, 239);
.demo-form-inline {
    background-color: white;
    display: flex;
    align-items: center;
    padding: 20px;
}
.demo-form-inline .el-input {
    --el-input-width: 220px;
}

.demo-form-inline .el-select {
    --el-select-width: 220px;
}
.el-table {
    // 修改表格移入移出背景色
    :deep(.el-table__body tr:hover) {
        background-color: #ffedec;
        color: #ff321f;
    }
    :deep(.el-table__body tr:hover > td.el-table__cell) {
        background-color: #ffedec;
    }
    .edit-but {
        color: #160907;
    }
}
// }
.pagination-contr {
    padding: 10px;
    background-color: white;
}
</style>
